/**
 * Main script for converting Pikachu Volleyball replay files to video
 * OFFLINE RENDERING (Fast) with Audio Synthesis
 * Uses 'mp4-muxer' library
 */
'use strict';
import { settings } from '@pixi/settings';
import { SCALE_MODES } from '@pixi/constants';
import { Renderer, BatchRenderer, autoDetectRenderer } from '@pixi/core';
import { Prepare } from '@pixi/prepare';
import { Container } from '@pixi/display';
import { Loader } from '@pixi/loaders';
import { SpritesheetLoader } from '@pixi/spritesheet';
import { CanvasRenderer } from '@pixi/canvas-renderer';
import { CanvasSpriteRenderer } from '@pixi/canvas-sprite';
import { CanvasPrepare } from '@pixi/canvas-prepare';
import '@pixi/canvas-display';
import { ASSETS_PATH } from '../offline_version_js/assets_path.js';
import { PikachuVolleyballReplay } from './pikavolley_replay.js';
import { setGetSpeechBubbleNeeded } from '../chat_display.js';
import { serialize } from '../utils/serialize.js';
import { getHashCode } from '../utils/hash_code.js';
import '../../style.css';

// npm install mp4-muxer 필요
import * as Mp4Muxer from 'mp4-muxer';

console.log('🎬 [INIT] Offline Renderer with Audio Hooking');

// [1] 전역 DOM 패치 (채팅 에러 방지)
const originalReplaceChild = Node.prototype.replaceChild;
Node.prototype.replaceChild = function(newChild, oldChild) {
    try { return originalReplaceChild.call(this, newChild, oldChild); } 
    catch (e) { try { this.appendChild(newChild); } catch (e2) {} return newChild; }
};
const originalRemoveChild = Node.prototype.removeChild;
Node.prototype.removeChild = function(child) {
    try { return originalRemoveChild.call(this, child); } catch (e) { return null; }
};

class OfflineConverter {
  constructor() {
    Renderer.registerPlugin('prepare', Prepare);
    Renderer.registerPlugin('batch', BatchRenderer);
    CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
    CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
    Loader.registerPlugin(SpritesheetLoader);
    settings.RESOLUTION = 2;
    settings.SCALE_MODE = SCALE_MODES.NEAREST;
    settings.ROUND_PIXELS = true;

    this.renderer = null;
    this.stage = null;
    this.loader = null;
    this.pikaVolley = null;
    
    this.audioLog = []; // { frame: number, key: string }
    this.decodedBuffers = {}; 
    
    this.muxer = null;
    this.videoEncoder = null;
    this.audioEncoder = null;
  }

  updateStatus(message) {
    const statusEl = document.getElementById('status');
    if (statusEl) statusEl.textContent = message;
    console.log(`📝 [STATUS] ${message}`);
  }

  handleRecordButtonClick() {
    const fileInput = document.createElement('input');
    fileInput.type = 'file';
    fileInput.accept = '.txt';
    fileInput.onchange = (e) => {
      // @ts-ignore
      const file = e.target.files[0];
      if (file) this.loadReplayFile(file);
    };
    fileInput.click();
  }

  loadReplayFile(file) {
    this.updateStatus('리플레이 분석 중...');
    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        // @ts-ignore
        const packWithComment = JSON.parse(event.target.result);
        const pack = packWithComment.pack;
        
        const originalHash = pack.hash;
        pack.hash = 0;
        if (originalHash !== getHashCode(serialize(pack))) {
             console.warn('Hash mismatch ignored.');
        }
        pack.hash = originalHash;

        this.initializeRenderer(pack);
      } catch (err) {
        console.error(err);
        this.updateStatus('파일 로딩 실패');
      }
    };
    reader.readAsText(file);
  }

  initializeRenderer(pack) {
    this.updateStatus('리소스 로딩 및 오디오 디코딩...');
    
    this.renderer = autoDetectRenderer({
      width: 432,
      height: 304,
      antialias: false,
      backgroundColor: 0x000000,
      forceCanvas: true,
      preserveDrawingBuffer: true,
    });

    this.stage = new Container();
    this.loader = new Loader();

    const container = document.querySelector('#game-canvas-container');
    // @ts-ignore
    container.innerHTML = '';
    // @ts-ignore
    container.appendChild(this.renderer.view);
    this.ensureDOMElements(container);

    this.loader.add(ASSETS_PATH.SPRITE_SHEET);
    
    // 오디오 파일 직접 디코딩 (OfflineAudioContext용)
    const soundPromises = [];
    // @ts-ignore
    const AudioContext = window.AudioContext || window.webkitAudioContext;
    const audioCtx = new AudioContext();
    
    for (const key in ASSETS_PATH.SOUNDS) {
        const url = ASSETS_PATH.SOUNDS[key];
        const p = fetch(url)
            .then(res => res.arrayBuffer())
            .then(buf => audioCtx.decodeAudioData(buf))
            .then(decoded => { this.decodedBuffers[key] = decoded; });
        soundPromises.push(p);
        this.loader.add(ASSETS_PATH.SOUNDS[key]); // PIXI용 로드
    }

    Promise.all(soundPromises).then(() => {
        this.loader.load(() => this.startProcessing(pack));
    });
  }

  ensureDOMElements(container) {
    const ids = ['player1-chat-box', 'player2-chat-box', 'player1-nickname', 'player2-nickname', 'player1-partial-ip', 'player2-partial-ip'];
    ids.forEach(id => {
        if (!document.getElementById(id)) {
            const el = document.createElement('div');
            el.id = id;
            el.style.display = 'none';
            container.appendChild(el);
        }
    });
  }

  async startProcessing(pack) {
    this.updateStatus('렌더링 준비 중...');
    
    this.pikaVolley = new PikachuVolleyballReplay(
      this.stage,
      this.loader.resources,
      pack.roomID,
      pack.nicknames,
      pack.partialPublicIPs,
      pack.inputs,
      pack.options,
      pack.chats
    );

    try {
        // @ts-ignore
        setGetSpeechBubbleNeeded(this.pikaVolley); 
    } catch(e){}
    this.pikaVolley.willDisplayChat = true;

    // [핵심] 오디오 로거 설치
    this.audioLog = [];
    this.hookAudio(this.pikaVolley);

    const fps = 30; // 고정 FPS (리플레이 표준)
    const totalFrames = pack.inputs.length;
    const duration = totalFrames / fps;

    // Muxer 설정
    // @ts-ignore
    this.muxer = new Mp4Muxer.Muxer({
        // @ts-ignore
        target: new Mp4Muxer.ArrayBufferTarget(),
        video: { codec: 'avc', width: 432, height: 304 },
        audio: { codec: 'aac', sampleRate: 44100, numberOfChannels: 2 },
        fastStart: 'in-memory',
    });

    this.videoEncoder = new VideoEncoder({
        output: (chunk, meta) => this.muxer.addVideoChunk(chunk, meta),
        error: (e) => console.error('Video Enc Error:', e)
    });
    this.videoEncoder.configure({
        codec: 'avc1.42001f',
        width: 432,
        height: 304,
        bitrate: 2_500_000,
        framerate: fps,
    });

    this.audioEncoder = new AudioEncoder({
        output: (chunk, meta) => this.muxer.addAudioChunk(chunk, meta),
        error: (e) => console.error('Audio Enc Error:', e)
    });
    this.audioEncoder.configure({
        codec: 'mp4a.40.2',
        sampleRate: 44100,
        numberOfChannels: 2,
        bitrate: 128_000,
    });

    // ----------------------------------------------------
    // 비디오 렌더링 루프
    // ----------------------------------------------------
    const progressBar = document.getElementById('progress-bar');
    
    for (let i = 0; i < totalFrames; i++) {
        // 인코더 대기열 관리
        if (this.videoEncoder.encodeQueueSize > 15) {
            await new Promise(r => setTimeout(r, 10));
        }

        // [중요] gameLoopSilent 내부에서는 fakeAudio를 사용하므로 소리 감지가 안 됨.
        // 따라서 여기서 우리가 만든 fakeAudio(Audio Hook)를 강제로 주입해야 함.
        const originalAudio = this.pikaVolley.audio;
        this.pikaVolley.gameLoopSilent(); 
        
        // *주의*: pikavolley_replay.js를 보면 gameLoopSilent가 끝나면 
        // this.audio를 원래대로 복구해버림. 하지만 우리는 gameLoopSilent '안에서' 일어나는 일을
        // 잡아야 하는데, gameLoopSilent는 fakeAudio를 써버림.
        // 
        // 해결책: PikachuVolleyballReplay 생성자에서 만든 this.fakeAudio 자체를 
        // 우리가 만든 '스파이 오디오'로 바꿔치기 해버리면 됨.
        
        this.renderer.render(this.stage);
        this.drawOverlaysOnCanvas();

        // 프레임 인코딩
        // @ts-ignore
        const bitmap = await createImageBitmap(this.renderer.view);
        const videoFrame = new VideoFrame(bitmap, { timestamp: (i * 1000000) / fps });
        this.videoEncoder.encode(videoFrame, { keyFrame: i % 90 === 0 });
        videoFrame.close();

        // UI 갱신 (30프레임마다)
        if (i % 30 === 0) {
            const pct = Math.floor((i / totalFrames) * 100);
            this.updateStatus(`영상 처리 중... ${pct}%`);
            // @ts-ignore
            if(progressBar) progressBar.style.width = `${pct}%`;
            await new Promise(r => setTimeout(r, 0));
        }
    }
    await this.videoEncoder.flush();

    // ----------------------------------------------------
    // 오디오 합성 및 인코딩
    // ----------------------------------------------------
    this.updateStatus('오디오 합성 중...');
    await this.renderAudio(duration, fps);

    // 저장
    this.muxer.finalize();
    const buffer = this.muxer.target.buffer;
    this.saveFile(buffer);
  }

  // [핵심] Replay 객체 내부의 fakeAudio를 낚아채서 로깅 기능을 심음
  hookAudio(replayInstance) {
    // 이미 생성자에서 만들어진 fakeAudio 객체를 가져옴
    const fakeAudio = replayInstance.fakeAudio;
    const sounds = fakeAudio.sounds;

    // 모든 효과음에 대해 후킹
    for (const key in sounds) {
        if (key === 'bgm') continue; // BGM은 무시 (원하면 로직 추가 가능)
        
        // play 함수 덮어쓰기
        sounds[key].play = () => {
            // 현재 프레임 번호를 가져옴 (gameLoopSilent가 실행 중일 때 호출됨)
            const currentFrame = replayInstance.replayFrameCounter;
            this.audioLog.push({
                key: key,
                frame: currentFrame
            });
        };
    }
  }

  async renderAudio(duration, fps) {
    if (this.audioLog.length === 0) return;

    const sampleRate = 44100;
    // 전체 길이에 1초 여유분 추가
    const length = Math.ceil(duration * sampleRate) + sampleRate;
    const offlineCtx = new OfflineAudioContext(2, length, sampleRate);

    // 기록된 로그를 바탕으로 오디오 스케줄링
    this.audioLog.forEach(log => {
        const buffer = this.decodedBuffers[log.key];
        if (buffer) {
            const source = offlineCtx.createBufferSource();
            source.buffer = buffer;
            source.connect(offlineCtx.destination);
            
            // 프레임 번호 -> 초 단위 시간으로 변환
            const startTime = log.frame / fps;
            source.start(startTime);
        }
    });
    console.log(this.audioLog);

    // 믹싱(렌더링) 수행
    const renderedBuffer = await offlineCtx.startRendering();

    // 인코딩 (1초 단위로 끊어서 처리)
    const chunkSize = 44100; 
    const lengthSamples = renderedBuffer.length;
    const numberOfChannels = renderedBuffer.numberOfChannels;

    for (let frame = 0; frame < lengthSamples; frame += chunkSize) {
        const size = Math.min(chunkSize, lengthSamples - frame);
        const planarData = new Float32Array(size * numberOfChannels);
        
        for (let ch = 0; ch < numberOfChannels; ch++) {
            const channelData = renderedBuffer.getChannelData(ch);
            planarData.set(channelData.subarray(frame, frame + size), ch * size);
        }

        const audioData = new AudioData({
            format: 'f32-planar',
            sampleRate: sampleRate,
            numberOfChannels: numberOfChannels,
            numberOfFrames: size,
            timestamp: (frame / sampleRate) * 1_000_000,
            data: planarData
        });

        this.audioEncoder.encode(audioData);
        audioData.close();
    }
    await this.audioEncoder.flush();
  }

  saveFile(buffer) {
    const blob = new Blob([buffer], { type: 'video/mp4' });
    const url = URL.createObjectURL(blob);
    
    const d = new Date();
    const dateStr = `${d.getFullYear()}${String(d.getMonth() + 1).padStart(2, '0')}${String(d.getDate()).padStart(2, '0')}`;
    const timeStr = `${String(d.getHours()).padStart(2, '0')}${String(d.getMinutes()).padStart(2, '0')}`;
    let filename = `${dateStr}_${timeStr}_pikavolley.mp4`;
    
    if (this.pikaVolley.nicknames && this.pikaVolley.nicknames.length === 2) {
        const n1 = this.pikaVolley.nicknames[0].replace(/[/\\?%*:|"<>]/g, '_');
        const n2 = this.pikaVolley.nicknames[1].replace(/[/\\?%*:|"<>]/g, '_');
        filename = `${dateStr}_${timeStr}_${n1}_vs_${n2}.mp4`;
    }

    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.click();
    
    this.updateStatus('완료!');
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = false;
    const loadingOverlay = document.getElementById('loading-overlay');
    // @ts-ignore
    if (loadingOverlay) loadingOverlay.classList.add('hidden');
    
    // 메모리 정리
    setTimeout(() => URL.revokeObjectURL(url), 1000);
  }

  drawOverlaysOnCanvas() {
    // @ts-ignore
    const ctx = this.renderer.view.getContext('2d');
    if (!ctx) return;

    ctx.save();
    ctx.font = 'bold 16px "Segoe UI", sans-serif';
    ctx.textAlign = 'center';
    ctx.textBaseline = 'top';

    const p1Nick = document.getElementById('player1-nickname')?.textContent || '';
    const p1IP = document.getElementById('player1-partial-ip')?.textContent || '';
    const p2Nick = document.getElementById('player2-nickname')?.textContent || '';
    const p2IP = document.getElementById('player2-partial-ip')?.textContent || '';

    // Player 1
    if (p1Nick) {
        ctx.fillStyle = 'white';
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 3;
        ctx.strokeText(p1Nick, 50, 10);
        ctx.fillText(p1Nick, 50, 10);
        if (p1IP) {
            ctx.font = '12px sans-serif';
            ctx.strokeText(p1IP, 50, 30);
            ctx.fillText(p1IP, 50, 30);
        }
    }

    // Player 2
    if (p2Nick) {
        ctx.font = 'bold 16px "Segoe UI", sans-serif';
        ctx.strokeText(p2Nick, 432 - 50, 10);
        ctx.fillText(p2Nick, 432 - 50, 10);
        if (p2IP) {
            ctx.font = '12px sans-serif';
            ctx.strokeText(p2IP, 432 - 50, 30);
            ctx.fillText(p2IP, 432 - 50, 30);
        }
    }

    this.drawChatBubble(ctx, 'player1-chat-box', 80, 60);
    this.drawChatBubble(ctx, 'player2-chat-box', 432 - 80, 60);
    ctx.restore();
  }

  drawChatBubble(ctx, elementId, x, y) {
      const el = document.getElementById(elementId);
      if (!el || !el.textContent || el.textContent.trim() === '') return;
      const text = el.textContent;
      ctx.font = '14px sans-serif';
      const width = ctx.measureText(text).width + 20;
      const height = 30;
      ctx.fillStyle = 'rgba(255, 255, 255, 0.9)';
      ctx.fillRect(x - width / 2, y, width, height);
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 1;
      ctx.strokeRect(x - width / 2, y, width, height);
      ctx.fillStyle = 'black';
      ctx.fillText(text, x, y + 6);
  }
}

function adjustAssetsPath() {
  const prefix = '../../';
  if (!ASSETS_PATH.SPRITE_SHEET.startsWith(prefix)) {
    const clean = (p) => p.replace(/^\.\.\//, '');
    ASSETS_PATH.SPRITE_SHEET = prefix + clean(ASSETS_PATH.SPRITE_SHEET);
    for (const k in ASSETS_PATH.SOUNDS) ASSETS_PATH.SOUNDS[k] = prefix + clean(ASSETS_PATH.SOUNDS[k]);
  }
}
adjustAssetsPath();

const converter = new OfflineConverter();
const recordBtn = document.getElementById('record-btn');
if (recordBtn) {
  recordBtn.addEventListener('click', () => converter.handleRecordButtonClick());
}